import { _decorator, Color, Component, Label, Sprite } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('ConnectorLetterComponent')
export class ConnectorLetterComponent extends Component {

    @property({type:Sprite})
    private sprite = null;

    @property({type:Label})
    private label = null;

    public letter: string;
    public selected: boolean;

    setLetter(letter: string) {
        this.letter = letter;
        this.label.string = this.letter.toUpperCase();
        this.changeState(false);
    }

    changeState(selected: boolean) {
        if (selected) this.sprite.color = new Color(255, 222, 138);
        else this.sprite.color = Color.TRANSPARENT;
        this.selected = selected;
    }
}

